a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
@font-face {
  font-family: "Silkscreen Web";
  src: url(../../public/fonts/slkscr-webfont.eot);
  src: url(../../public/fonts/slkscr-webfont.eot?#iefix)
      format("embedded-opentype"),
    url(../../public/fonts/slkscr-webfont.woff) format("woff"),
    url(../../public/fonts/slkscr-webfont.ttf) format("truetype"),
    url(../../public/fonts/slkscr-webfont.svg#silkscreennormal) format("svg");
  font-weight: 400;
  font-style: normal;
}
html {
  font-size: 16px;
}
body {
  align-items: center;
  display: flex;
  font-family: "Silkscreen Web", monotype;
  height: 100vh;
  justify-content: center;
  width: 100%;
  background: linear-gradient(140deg, #1d725c, #a1bd2f, #6112ae, #1298ae);
  background-size: 400% 400%;
  -webkit-animation: gradientAnimation 1s ease infinite;
  -moz-animation: gradientAnimation 1s ease infinite;
  -o-animation: gradientAnimation 1s ease infinite;
  animation: gradientAnimation 1s ease infinite;
}
h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  text-align: center;
}
@-webkit-keyframes gradientAnimation {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0 0;
  }
}
@-moz-keyframes gradientAnimation {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 0;
  }
}
@-o-keyframes gradientAnimation {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 50% 50%;
  }
  100% {
    background-position: 0 0;
  }
}
@keyframes gradientAnimation {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0 0;
  }
}
